.header {
  margin: 0;
  padding: 2em;
  transition: background-color 0.8s ease-out;
}

.header__color {
  margin: inherit;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.055em;
}

.header__subtext {
  background-color: #fff;
  max-width: 10em;
  padding-left: 1.5em;
}
